<template>
  <div class="qs-list">
    <ul v-if="qsList.length == 0 ? false : true">
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li><span @click="$router.push({name: 'qsEdit', params: {num: 0}})">+</span></li>
    </ul>
    <template v-for="item in qsList">
      <ul>
        <li><input type="checkbox" v-model="item.checked"></li>
        <li>{{item.title}}</li>
        <li>{{item.time}}</li>
        <li :class="item.state === 'inissue' ? 'inissue' : ''">{{item.stateTitle}}</li>
        <li>
          <button @click="iterator = edit(item); iterator.next()"></button>
          <button @click="iterator = delItem(item.num); iterator.next()"></button>
          <router-link :to="`/fill/${item.num}`" tag="button"></router-link>
          <button @click="iterator = watchData(item); iterator.next()"></button>
        </li>
      </ul>
    </template>
    <div class="list-bottom" v-if="qsList.length == 0 ? false : true">
      <label><input type="checkbox" id="all-check" v-model="selectAll"></label>
      <button @click="iterator = delItems(); iterator.next()"></button>
    </div>
      <div class="add-qs" v-if="qsList.length === 0">
        <button class="add-btn" 
        @click="$router.push({name: 'qsEdit', params: {num: 0}})">+&nbsp;&nbsp;</button>
      </div>
    <div class="shadow" v-if="showDialog">
      <div class="del-dialog">
        <header>
          <span></span>
          <span class="close-btn" @click="showDialog = false">X</span>
        </header>
        <p>{{info}}</p>
        <div class="btn-box">
          <button class="yes" @click="iterator.next();"></button>
          <button @click="showDialog = false"></button>
        </div>
      </div>
    </div>
  </div>
</template>

