<template>
  <div class="fill-container">
    <div class="fill" v-if="!isError">
      <router-link to="/" tag="span" class="back">&lt; </router-link>
      <h2>{{qsItem.title}}</h2>
      <div class="content-header">
      </br>
      <h3></h3>
      <p></p>
      <p></p>
    </div>
      <div class="content">
        <div class="content-item" v-for="item in qsItem.question">
          <p class="qs-title" :name="`${item.values}`">
            {{item.num}}&nbsp;{{item.title}}&nbsp;{{getMsg(item)}}
          </p>
          <p v-for="(option,index) in item.options" class="option">
            <label>
              <input 
              type="radio" 
              :name="`${item.num}-${item.title}`"
              v-model="requiredItem[item.num]"
              v-if="item.type === 'radio'"
              :value="`${option[1]}`"
              :class="`${item.values}`">
              <input 
              type="checkbox" 
              :name="`${item.num}-${item.title}`"
              v-model="requiredItem[item.num]"
              v-if="item.type === 'checkbox'"
              :value="option">{{option[0]}}
            </label>
          </p>
          <textarea 
          v-if="item.type === 'textarea'" 
          v-model="requiredItem[item.num]"></textarea>
        </div>
      </div>
      <transition name="fade">
        <div class="dialog" v-if="showDialog">
          <div class="submit-dialog" v-if="submitError">
            <header>
              <span></span>
              <span class="close-btn" @click="showDialog = false">X</span>
            </header>
            <p>{{info}}</p>
            <div class="btn-box">
              <button class="yes" @click="showDialog = false"></button>
              <button @click="showDialog = false"></button>
            </div>
          </div>
          <div class="submit-dialog" v-else>
            <header>
              <span></span>
              <span class="close-btn" @click="showDialog = false">X</span>
            </header>
            <p>{{info}}</p>
          </div>
        </div>
      </transition>  
      <footer>
        <button @click="submit" class="submit"></button>
      </footer>
    </div>
    <div class="error" v-else>
      404 Not Found
    </div>
  </div>
</template>

<script>
import storage from '../store.js'
import jquery from 'jquery'

/**
 * A module that define qs-fill router view
 * @exports qs-fill
 * @author oyh(Reusjs)
 */
  export default {
    name: 'qsFill',
    data() {
      return {
        qsItem: [],
        qsList: storage.get(),
        isError: false,
        showDialog: false,
        info: '',
        submitError: false,
        requiredItem: {}
      }
    },
    created() {
      this.fetchData()
    },
    mounted() {
      this.getRequiredItem()
    },
    methods: {
      fetchData() {
        let i = 0;
        for (let length = this.qsList.length; i < length; i++) {
          if (this.qsList[i].num == this.$route.params.num) {
            this.qsItem = this.qsList[i]
            break
          }
        }
        if (i === this.qsList.length) this.isError = true
      },
      getMsg(item) {
        let msg = ''
        if (item.type === 'radio') {
          msg = '()'
        } else if (item.type === 'checkbox') {
          msg = '()'
        } else {
          msg = '()'
        }

        return item.isNeed ? `${msg} *` : msg
      },
  
      submit() {
        if (this.qsItem.state === 'inissue') {
          let result = this.validate()
          // result = true  
  
          if (result) {
            this.showDialog = true
            this.submitError = false
            // this.info = ''
            var cat = {'A':'','B':'','C':'','D':'','E':'oh,'}

            var countA = 0
            var countB = 0
            var countC = 0
            var countD = 0
            var arr = new Array()

            function descend(x,y){
                return y[1] - x[1];  //2
            }

            $('input.A:radio:checked').each(function(){      
                countA+=parseInt($(this).val()-2)            
            })
            arr[0] = ['A',countA]
            $('input.B:radio:checked').each(function(){      
                countB+=parseInt($(this).val()-2)            
            })
            arr[1] = ['B',countB]
            $('input.C:radio:checked').each(function(){      
                countC+=parseInt($(this).val()-2)            
            })
            arr[2] = ['C',countC]
            $('input.D:radio:checked').each(function(){      
                countD+=parseInt($(this).val()-2)            
            })
            arr[3] = ['D',countD]
            arr.sort(descend) //

            // 
            if (arr[0][1]-arr[1][1]>=4){
              // alert(arr[0][0])
              storage.save([{'res':cat[arr[0][0]]}],'result')
              // this.info = ''
              
            }
            else{
              if (arr[1][1]-arr[2][1]>=4){
                // alert(arr[0][0]+arr[1][0])
                storage.save([{'res':cat[arr[0][0]]+cat[arr[1][0]]}],'result')
                // this.info = ''
              }
              else{
                // alert('')
                storage.save([{'res':cat['E']}],'result')
                // this.info = ''
              }
              }

            // alert(arr)
            
            // storage.save([{'res':cat.A}],'result')
            // if (count<=3){
            //   this.info = ''
            //   storage.save([{'res':cat.A+' '+':'+count}],'result')
            // }
            // else {
            //   this.info = cat.B+' '+':'+count
            // }
            
            this.info = ''
            setTimeout(() => {
              this.showDialog = false
            }, 1000)
            setTimeout(() => {
              this.$router.push({path: '/result'})
            }, 1200)
          } else {
            this.showDialog = true
            this.submitError = true
            this.info = ' '
          }
        } else {
          this.showDialog = true
          this.submitError = true
          this.info = ' '
        }
      },
      getRequiredItem() {
        this.qsItem.question.forEach( item => {
          if (item.isNeed) {
            if (item.isNeed) {
              if (item.type === 'checkbox') {
                this.requiredItem[item.num] = []
              } else {
                this.requiredItem[item.num] = ''
              }
            }
          }
        } )
      },
      validate() {
        for (let i in this.requiredItem) {
          if (this.requiredItem[i].length === 0) return false
        }
        return true
      }
    },
    watch: {
      '$route': 'fetchData'
    }
  }
</script>

<style lang="scss" scoped>
@import '../style/QS-fill'
</style>


